html {
    scroll-behavior: smooth;
}

body {
    padding-top: 76px;
}

nav.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
}

#wrap {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

#wrap > footer {
    margin-top: auto;
}

.custom-logo {
    max-height: 50px;
}

.course-main {
    z-index: 5;
}

.course-featured {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 30%;
    right: 0;
    z-index: 1;
    pointer-events: none;
}

[data-videobg] {
    overflow: hidden;
}

[data-videobg] iframe {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    transform: translate(-50%, -50%);
}

.course-featured:before {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 5;
    background: rgb(0, 0, 0, 0.6);
}

.courses-list .course-details {
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #fff;
    padding: 5px 10px 0 10px;
}

.navigation .thumbnail {
    max-width: 100%;
    width: 100px;
}

/* Ecommerce */

.alert-authentication {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 99;
    background-color: rgba(255, 255, 255, 0.85);
}

.alert-authentication-content {
    max-width: 450px;
    margin: 0 auto;
}

/* Ecommerce end */

#video-box {
    line-height: 1;
}

.videolessons-play-first-video {
    cursor: pointer;
}

.videolessons-play-course-box {
    background-color: rgba(0, 0, 0, 0.5);
}

.videolessons-videos .element {
    cursor: pointer;
}

.videolessons-videos .element.active {
    background-color: #cccccc;
}

.videolessons-videos .element .thumbnail {
    max-width: 100%;
    width: 100px;
}

.videolessons-list.placeholder {
    position: relative;
}

.videolessons-list.placeholder:before {
    content: "";
    position: absolute;
    z-index: 99;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.3);
    background: -moz-linear-gradient(
        0deg,
        rgba(255, 255, 255, 1) 10%,
        rgba(255, 255, 255, 0.4) 100%
    );
    background: -webkit-linear-gradient(
        0deg,
        rgba(255, 255, 255, 1) 10%,
        rgba(255, 255, 255, 0.4) 100%
    );
    background: linear-gradient(
        0deg,
        rgba(255, 255, 255, 1) 10%,
        rgba(255, 255, 255, 0.4) 100%
    );
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#ffffff",GradientType=1);
}

/* Front page */

.front-jumbo__bg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
}

.front-jumbo__bg:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 10;
}

.front-jumbo__content {
    z-index: 5;
}

/* Front page end */

footer .footer-widgets a {
    color: rgb(176, 176, 176);
}

footer .footer-widgets .widget-title {
    font-size: 1.2rem;
    text-transform: uppercase;
}

footer .footer-widgets ul.menu {
    list-style: none;
    padding: 0;
}

footer .footer-logos > svg {
    max-height: 30px;
    display: inline-block;
    width: auto;
    margin: 0 0.3rem;
}

@import "responsive";
